.form-saving {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  align-self: center;
  justify-self: center;
  width: 100%;
}

.form-input {
  border: none;
  border-radius: 8px;
  width: 12rem;
  height: 2rem;
}

.update-savings {
  cursor: pointer;
  /* border: none; */
  color: #eee;
  background-color: #e03131;
  font-size: 1.6rem;
  font-weight: bold;
  border-radius: 10px;
  width: 12rem;
  height: 4rem;
  box-shadow: #eee;
}

.update-savings:hover {
  background-color: #b52626;
}
